.footer {
  width: 100%;
  height: 200px;
  background-color: rgb(31, 32, 33);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 10px;
}

.socialMedia svg {
  color: white;
  margin: 20px;
  font-size: 70px;
  cursor: pointer;
}

.footer p {
  color: white;
}

@media only screen and (max-width: 450px) {
  .socialMedia svg {
    font-size: 50px;
  }
}
