.header {
  margin-top: 60px;
}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Lora", serif;
  color: #444;
}

.headerTitleSm {
  position: absolute;
  top: 17%;
  font-size: 20px;
}

.headerTitleLg {
  position: absolute;
  top: 20%;
  font-size: 100px;
}

.headerImg {
  width: 100%;
  height: 450px;
  margin-top: 40px;
  object-fit: cover;
}

@media only screen and (max-width: 450px) {
  .headerTitleSm {
    top: 20%;
  }

  .headerTitleLg {
    top: 23%;
  }
}
